/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #000;
  font-family: "Averia Sans Libre";
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 1.5;
}

@media screen and (min-width: 600px) {
  body,
  button,
  input,
  select,
  textarea {
    font-size: 20px;
    font-size: 1.25rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: .75em;
  margin-bottom: .25em;
  font-family: "Averia Sans Libre";
  clear: both;
}

h1 {
  font-size: 60px;
  font-size: 3.75rem;
}

h2 {
  font-size: 52px;
  font-size: 3.25rem;
}

h3 {
  font-size: 44px;
  font-size: 2.75rem;
}

h4 {
  font-size: 36px;
  font-size: 2.25rem;
}

h5 {
  font-size: 28px;
  font-size: 1.75rem;
}

h6 {
  font-size: 20px;
  font-size: 1.25rem;
}

p {
  font-family: Lato;
  margin-top: .5em;
  margin-bottom: 1em;
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  position: relative;
  margin: 0 0 1em;
  background: #eee;
  padding: 1em 2em .5em 5em;
  font-style: italic;
}

blockquote:before {
  position: absolute;
  top: -.2em;
  left: -.13em;
  content: "“";
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  font-size: 12em;
  line-height: 100%;
  color: white;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 18.75px;
  font-size: 1.17188rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 18.75px;
  font-size: 1.17188rem;
}

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark, ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background-size: 412px;
}

blockquote, q {
  quotes: "" "";
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul,
ol {
  margin: 0 0 1.5em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: .5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

figure {
  margin: 0;
  /* Extra wide images within figure tags don't overflow the content area. */
}

table {
  border-collapse: collapse;
  margin: 0 0 1.5em;
  width: 100%;
}

thread th {
  border-bottom: 2px solid #bbb;
  padding-bottom: .5em;
}

td,
th {
  vertical-align: top;
}

th {
  padding: .4em;
  text-align: left;
}

tr {
  border-bottom: 1px solid #eee;
}

td {
  padding: .4em;
}

th:first-child,
td:first-child {
  padding-left: 0;
}

th:last-child,
td:last-child {
  padding-right: 0;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid;
  border-color: black;
  background: transparent;
  color: black;
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 1;
  padding: .5em 1em;
}

button:hover, button:active, button:focus,
input[type="button"]:hover,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
  background: white;
  color: black;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

textarea {
  width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  text-decoration: none;
}

a:hover, a:focus, a:active {
  color: #41CA86;
  text-decoration: underline;
}

a:focus {
  outline: thin dotted;
}

a:hover, a:active {
  outline: 0;
}

.page-content a,
.entry-content a,
.entry-summary a,
.comment-content a {
  text-decoration: none;
}

.page-content a:focus, .page-content a:hover,
.entry-content a:focus,
.entry-content a:hover,
.entry-summary a:focus,
.entry-summary a:hover,
.comment-content a:focus,
.comment-content a:hover {
  color: #41CA86;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #000;
  display: block;
  font-size: 17.5px;
  font-size: 1.09375rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layouts
--------------------------------------------------------------*/
.site-content {
  max-width: 1200px;
  margin: auto;
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
  position: relative;
  font-family: "Averia Sans Libre";
  background-color: #fff;
}

/*
 *	Header with site info
*/
.top-header {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1em 0;
  color: black;
  background-color: white;
  display: flex;
  justify-content: space-between;
}

.top-header .site-branding .custom-logo-link {
  max-width: 100px;
  display: inline-block;
  margin-right: 1em;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.top-header .site-branding .custom-logo-link img {
  height: 100%;
}

.top-header .site-branding .text {
  display: inline-block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  vertical-align: top;
}

.top-header .site-branding .text .site-title {
  margin: 0;
  padding: 0;
  font-weight: 900;
  line-height: 1em;
}

.top-header .site-branding .text .site-title a {
  color: #000;
}

.top-header .site-branding .text .site-description {
  margin: 0;
  padding-top: 2%;
  font-style: italic;
  font-weight: 100;
  display: inline-block;
}

.top-header .contact-header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-header .contact-header a {
  color: black;
  position: relative;
}

.top-header .contact-header a span {
  position: absolute;
  top: 0;
  left: -40px;
  font-size: 30px;
}

@media screen and (max-width: 1300px) {
  .top-header .site-branding .text {
    display: inline-block;
  }
  .top-header .site-branding .text .site-title {
    font-size: 50px;
    font-size: 3.125rem;
  }
  .top-header .site-branding .text .site-description {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 1160px) {
  .top-header .site-branding .text {
    display: inline-block;
  }
  .top-header .site-branding .text .site-title {
    font-size: 40px;
    font-size: 2.5rem;
  }
  .top-header .site-branding .text .site-description {
    font-size: 20px;
    font-size: 1.25rem;
  }
  .top-header .contact-header a {
    font-size: 16px;
    font-size: 1rem;
  }
}

@media screen and (max-width: 920px) {
  .top-header .contact-header a {
    display: none;
  }
}

@media screen and (max-width: 690px) {
  .top-header {
    display: none;
  }
}

/*
 *	Navigation Bar
*/
.main-navigation {
  display: block;
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  font-weight: 400;
  clear: left;
  background-color: #002254;
}

.main-navigation ul {
  padding: 0;
  position: relative;
  display: none;
  list-style: none;
  margin: 0;
}

.main-navigation ul a {
  transition: background-color 0.15s ease-out;
}

.main-navigation ul a:hover {
  background-color: #41CA86;
}

.main-navigation ul .current-menu-item {
  background-color: #41CA86;
}

.main-navigation ul .cta {
  margin-left: 1.5em;
}

.main-navigation ul .cta a {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: #41CA86;
  border-radius: 5px;
  padding: 0.5em 1.5em;
  text-transform: uppercase;
  font-size: 18px;
}

.main-navigation ul .cta a:hover {
  text-decoration: none;
  opacity: 0.8;
}

.main-navigation ul ul {
  display: none;
  top: 1.5em;
  z-index: 99999;
}

.main-navigation ul ul ul {
  top: 0;
}

.main-navigation ul ul li {
  padding-left: 1em;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
  left: 100%;
}

.main-navigation ul ul a {
  width: 200px;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
  left: auto;
}

.main-navigation li {
  position: relative;
}

.main-navigation a {
  display: inline-block;
  width: 100%;
  padding: 1em 1.5em;
  text-decoration: none;
  color: #fff;
}

.main-navigation .menu-item-has-children,
.main-navigation .page_item_has_children {
  min-width: 218px;
}

.main-navigation .menu-item-has-children > a,
.main-navigation .page_item_has_children > a {
  padding-right: 2em;
}

button.dropdown-toggle {
  position: absolute;
  right: 0;
  border: none;
  background: inherit;
  color: white;
  line-height: 1.5em;
  padding: .4em 1em .4em .5em;
}

.menu-toggle {
  display: block;
  padding: .6em .8em;
  text-transform: uppercase;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on {
  display: block;
}

.menu-toggle {
  display: none;
}

.main-navigation .menu-navigation-container {
  max-width: 1200px;
  margin: auto;
}

.main-navigation .menu-item-has-children > a,
.main-navigation .page_item_has_children > a {
  padding-right: 2em;
  background: rgba(255, 255, 255, 0.1);
}

.main-navigation ul {
  display: block;
  display: flex;
  flex-wrap: wrap;
}

.main-navigation ul ul {
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  margin-left: 0;
}

.main-navigation ul ul li {
  padding-left: 0;
}

.main-navigation ul ul li a {
  width: 218px;
  background: none;
}

@media screen and (max-width: 690px) {
  .main-navigation .menu-toggle {
    display: block;
  }
  .main-navigation .menu-navigation-container {
    display: none;
  }
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: inline-block;
  width: 60%;
}

.entry-header {
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.cat-links {
  font-size: 90%;
  font-weight: 700;
}

.cat-links a {
  text-decoration: none;
  text-transform: uppercase;
  color: #41CA86;
}

.cat-links a:focus, .cat-links a:hover {
  background-color: #41CA86;
  color: white;
}

.entry-title {
  margin: .125em 0 .25em;
  font-size: 2.5em;
}

@media screen and (min-width: 900px) {
  .entry-title {
    font-size: 3em;
  }
}

.entry-title a {
  text-decoration: none;
  color: black;
}

.entry-title a:focus, .entry-title a:hover {
  border-bottom: 5px solid #41CA86;
}

.entry-meta,
.entry-footer {
  font-size: 90%;
}

.entry-meta a,
.entry-footer a {
  font-weight: 700;
  text-decoration: none;
  color: black;
}

.entry-meta a:focus, .entry-meta a:hover,
.entry-footer a:focus,
.entry-footer a:hover {
  color: black;
  border-bottom: 3px solid #41CA86;
}

.byline::after {
  content: "|";
  margin: 0 .5em;
}

.comments-link::before,
.edit-link::before {
  content: "|";
  margin: 0 .5em;
}

.byline,
.updated:not(.published) {
  display: none;
}

.single .byline,
.group-blog .byline {
  display: inline;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

.entry-footer {
  margin: 1.5em 0;
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.main-post {
  background-color: #fff;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
  position: relative;
  font-size: .8em;
  font-family: "Averia Sans Libre";
  color: #fff;
  background-color: #08082E;
  text-align: center;
}

.site-footer a {
  transition: color 0.15s ease-out;
  color: #fff;
  text-decoration: none;
}

.site-footer a:focus, .site-footer a:hover {
  color: #41CA86;
}

.site-footer .container {
  padding: 5em 0;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.site-footer .social-menu {
  width: 180px;
}

.site-footer .social-menu ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.site-footer .social-menu ul li {
  width: 100%;
}

.site-footer .social-menu .fa::before {
  pointer-events: none;
  margin-top: 8px;
  font-size: 2em;
  float: left;
  width: 30px;
}

.site-footer .social-menu a {
  padding: .5em 0 .5em 1.2em;
  font-family: "Averia Sans Libre";
  font-size: 1.5em;
  display: block;
  padding: .5em 1em;
  text-decoration: none;
}

.site-footer .site-info {
  font-family: Lato;
  font-size: 12px;
  color: #8A8A8A;
  padding: 1.5em 0;
  background-color: black;
}

.site-footer .donate {
  font-size: 1.4em;
}

.site-footer .email {
  order: 1;
  text-align: right;
  font-size: 1.4em;
}

.site-footer .email a {
  position: relative;
}

.site-footer .email a span {
  position: absolute;
  left: -40px;
  font-size: 30px;
  margin-right: 10px;
}

@media screen and (max-width: 850px) {
  .site-footer .container {
    padding: 1em;
    display: block;
  }
  .site-footer .container > * {
    margin: 10px;
  }
  .site-footer .container .social-menu {
    font-size: 14px;
    font-size: 0.875rem;
  }
  .site-footer .container .email {
    font-size: 20px;
    font-size: 1.25rem;
  }
}

/*--------------------------------------------------------------
## Sidebar
--------------------------------------------------------------*/
aside#secondary.widget-area {
  float: right;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.home .site-content {
  margin-top: 2em;
  margin-bottom: 2em;
}

.home .site-content .entry-content .top-info {
  display: flex;
}

.home .site-content .entry-content .top-info img {
  padding-right: 2em;
}

.home .site-content .entry-content .top-info .container {
  display: flex;
  flex-direction: column;
}

.home .site-content .entry-content .top-info .container div {
  padding: 0 2em 0;
  flex: 50%;
}

.home .site-content .entry-content .top-info .container div:first-child {
  background-color: #41CA86;
  margin-bottom: 1em;
}

.home .site-content .entry-content .top-info .container div:last-child {
  margin-top: 1em;
  background-color: #F9EA09;
}

.home .site-content .entry-content .top-info .container div h2 {
  margin: 0;
}

.home .site-content .entry-content .page-content {
  width: 100%;
  text-align: center;
}

.home .site-content .entry-content .page-content h1 {
  text-align: left;
}

.home .site-content .entry-content .page-content .main-post {
  transition: 150ms box-shadow ease-out, 150ms transform ease-out, 150ms border ease-out;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.home .site-content .entry-content .page-content .main-post img {
  width: 1200px;
  height: 600px;
  margin: auto;
}

.home .site-content .entry-content .page-content .main-post .main-post-text {
  color: #000;
  text-align: left;
  padding: 3em;
  width: 650px;
  height: 350px;
  float: right;
  position: absolute;
  right: 0px;
  bottom: 0px;
  background-color: #FFFFFF;
  opacity: 0.9;
}

.home .site-content .entry-content .page-content .main-post .publiced-date {
  font-size: 25px;
  font-weight: bold;
  font-family: "Averia Sans Libre";
}

.home .site-content .entry-content .page-content .main-post .category {
  padding: 0;
  color: #2E2EEE;
  font-weight: bold;
  float: right;
  font-size: 25px;
  margin: -2em;
}

.home .site-content .entry-content .page-content .main-post:hover {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.4), 0 8px 22px 0 rgba(0, 0, 0, 0.21);
  transform: scale(1.02);
  border: 1px solid #41CA86;
}

.home .site-content .basic-post {
  height: 309px;
  background-color: #FFFFFF;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 50px;
}

.home .site-content .basic-post .post-img {
  float: left;
  height: 309px;
  width: 600px;
}

.home .site-content .basic-post .post-text {
  margin-top: 3em;
  color: #000;
  text-align: left;
  padding-left: 3em;
  float: right;
  width: 600px;
}

.home .site-content .basic-post .post-text h4 {
  margin-top: .1em;
}

.home .site-content .basic-post .publiced-date {
  font-size: 20px;
  font-weight: bold;
  font-family: "Averia Sans Libre";
}

.home .site-content .basic-post .category {
  padding: 0;
  color: #2E2EEE;
  font-weight: bold;
  float: right;
  font-size: 25px;
  margin-top: -2em;
  padding-right: .8em;
}

.home .site-content .more-post {
  margin-top: 50px;
  position: relative;
  transform: translateY(-50%);
  background-color: #41CA86;
  border-radius: 5px;
  padding: 0.5em 1.5em;
  text-transform: uppercase;
  font-size: 20px;
  color: #FFFFFF;
  border: none;
}

/*--------------------------------------------------------------
## Slider
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5em;
  /* Make sure select elements fit in widgets. */
}

.widget select {
  max-width: 100%;
}

#wp-calendar {
  color: #666;
  font-size: 12px;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
}

.widget_calendar {
  width: 400px;
}

#wp-calendar a {
  color: #467b89;
}

#wp-calendar caption {
  text-transform: capitalize;
  background: #383838;
  color: #e0e0e0;
  font-size: 14px;
  padding: 10px 0;
  text-align: center;
  text-shadow: 0 -1px 0 #000;
  -webkit-box-shadow: inset 0 1px 0 #484848, 0 -1px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: inset 0 1px 0 #484848, 0 -1px 5px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 1px 0 #484848, 0 -1px 5px rgba(0, 0, 0, 0.3);
  border: 1px solid #373737;
  z-index: -99;
}

#wp-calendar thead th {
  font-size: 8px;
  padding: 5px 0;
  color: #666;
  text-transform: uppercase;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid #bbbbbb;
  background: #f7f7f7;
  background: -webkit-gradient(linear, left top, left bottom, from(#f7f7f7), to(#f1f1f1));
  background: -moz-linear-gradient(top, #f7f7f7, #f1f1f1);
  text-align: center;
}

#wp-calendar tbody td {
  color: #666;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  padding: 8px;
  text-align: center;
  font-weight: bold;
  border: 1px solid;
  border-color: #fff #bbbbbb #bbbbbb #fff;
  background: #ededed;
  background: -webkit-gradient(linear, left top, left bottom, from(#ededed), to(#dedede));
  background: -moz-linear-gradient(top, #ededed, #dedede);
}

#wp-calendar tbody td.pad {
  background: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#f5f5f5), to(#ececec));
  background: -moz-linear-gradient(top, #f5f5f5, #ececec);
}

#wp-calendar tfoot {
  color: #e0e0e0;
  font-size: 12px;
  text-align: center;
}

#wp-calendar tfoot tr {
  background: #f5f5f5;
  background: -webkit-gradient(linear, left top, left bottom, from(#f5f5f5), to(#ececec));
  background: -moz-linear-gradient(top, #f5f5f5, #ececec);
}

#wp-calendar tfoot td {
  padding: 10px 10px;
}

#wp-calendar tfoot a {
  color: #666;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
}

#wp-calendar tfoot td#prev {
  text-align: left;
}

#wp-calendar tfoot td#next {
  text-align: right;
}

#wp-calendar #today {
  color: #fff;
  border: 1px solid #467b89;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
  background: #6eafbf;
  background: -moz-radial-gradient(50% 50% 0deg, ellipse cover, #6eafbf, #569EB1);
  background: -webkit-gradient(radial, 50% 50%, 0, 50% 50%, 20, from(#6eafbf), to(#569EB1));
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 2em;
  max-width: 100%;
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  font-size: 90%;
  color: #666666;
  text-align: center;
}

.wp-caption a {
  color: inherit;
}

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption .wp-caption-text {
  margin: .5em 0 1.5em 1em;
}

@media screen and (min-width: 600px) {
  .wp-caption .wp-caption-text {
    margin-left: 2em;
  }
}

@media screen and (min-width: 900px) {
  .wp-caption .wp-caption-text {
    margin-left: 0;
  }
}

/*--------------------------------------------------------------
## Slider
--------------------------------------------------------------*/
.flex-caption {
  background: tomato;
  color: white;
  position: absolute;
  bottom: 0;
  padding: 1em;
  width: 50vh;
  height: 50vh;
}

.flexslider .slides img {
  width: auto;
}

.flex-control-nav {
  width: 50%;
}

/*--------------------------------------------------------------
## Images
--------------------------------------------------------------*/
.wp-caption.aligncenter,
.centered-image {
  max-width: 100vw;
  margin: 0 -1em;
}

@media screen and (min-width: 600px) {
  .wp-caption.aligncenter,
  .centered-image {
    margin: 0 -2em;
  }
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin: 1.5em 0;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
  padding: 1em;
}

.gallery-item img {
  display: block;
}

.gallery-item a {
  display: inline-block;
  border-bottom: none;
}

.gallery-item a:hover, .gallery-item a:focus {
  border-bottom: none;
  box-shadow: none;
}

.gallery-item a:hover img, .gallery-item a:focus img {
  border-color: #41CA86;
}

.gallery-item a img {
  padding: .5em;
  margin: 0 auto;
  border: 1px solid #fff;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
  margin: 0;
  font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  border: none;
}
